import Grid from "@mui/material/Unstable_Grid2";
import React from "react";
import TaskDrawerContent from "./taskDrawerContent";

const TaskCard = ({
    task,
    setUpgradeDrawer,
    theme,
    axios,
    friendLength,
    setSnackBar,
    tasks,
    setTasks,
    setBalance,
    balance,
    colors,
}) => {
    const telegram = window.Telegram.WebApp;
    return (
        <Grid xs={12}>
            <Grid
                container="true"
                sx={{
                    background: "rgba(50, 50, 50, 0.20)",
                    borderRadius: "10px",
                    boxShadow: "0 4px 30px rgba( 25, 25, 25, 0.8)",
                    backdropFilter: "blur(3px)",
                    WebkitBackdropFilter: "blur(3px)",
                    width: "100%",
                    height: "100%",
                    padding: 0.3,
                    border:
                        "1px solid " + (task.claimed ? "white" : `rgb(${colors[theme]})`),
                    display: "flex",
                    justifyContent: "center",
                    alignItems: "center",
                }}
            >
                <Grid
                    xs={3}
                    sx={{
                        display: "flex",
                        alignItems: "center",
                    }}
                >
                    <img height={"60rem"} src={task.image} alt="" />
                </Grid>
                <Grid xs={5.7}>
                    <span
                        style={{
                            textAlign: "center",
                            fontSize: "14px",
                            letterSpacing: "1px",
                            // whiteSpace: "nowrap",
                        }}
                    >
                        {task.title}
                    </span>
                </Grid>
                <Grid xs={3}>
                    <button
                        className={task.claimed ? "btn white" : "btn " + theme}
                        disabled={task.claimed && task.type != 2}
                        onClick={() => {
                            switch (task.type) {
                                case 0:
                                    telegram.openTelegramLink(task.data.url);
                                    break;
                                case 1:
                                    telegram.openLink(task.data.url);
                                    break;
                            }
                            setUpgradeDrawer([
                                true,
                                <TaskDrawerContent
                                    setBalance={setBalance}
                                    balance={balance}
                                    setUpgradeDrawer={setUpgradeDrawer}
                                    theme={theme}
                                    setSnackBar={setSnackBar}
                                    task={task}
                                    friendLength={friendLength}
                                    axios={axios}
                                    tasks={tasks}
                                    setTasks={setTasks}
                                />,
                            ]);
                        }}
                        style={{
                            padding: "7px 10px 7px 10px",
                            color: "white",
                            fontWeight: 600,
                            fontFamily: "sans-serif",
                            letterSpacing: "1px",
                            fontSize: "1.05rem",
                        }}
                    >
                        {task.claimed
                            ? "Done"
                            : task.type == 3
                            ? friendLength >= task.data.minimum
                                ? "Claim"
                                : `${friendLength} / ${task.data.minimum}`
                            : task.type == 2
                            ? "Claim"
                            : "Start"}
                    </button>
                </Grid>
            </Grid>
        </Grid>
    );
};

export default TaskCard;
