import Grid from "@mui/material/Unstable_Grid2";
import Particles from "@tsparticles/react";
import { useMemo, useState } from "react";
import TaskCard from "./taskCard";
import { Paper, Slide, SwipeableDrawer } from "@mui/material";
import { grey } from "@mui/material/colors";
import { TypeAnimation } from "react-type-animation";

const Tasks = ({
    theme,
    transition,
    P,
    colors,
    friendLength,
    axios,
    setSnackBar,
    setBalance,
    balance,
    tasks,
    setTasks,
}) => {
    const Particle = useMemo(() => <Particles options={P} />, []);
    const [upgradeDrawer, setUpgradeDrawer] = useState([false, <></>]);

    return (
        <div>
            {Particle}
            <Grid
                container="true"
                sx={{
                    height: "100vh",
                    bottom: 0,
                    display: "flex",
                    justifyContent: "center",
                    padding: ".7rem .7rem 5rem .7rem",
                    background: "none",
                    backdropFilter: "blur(0px)",
                    WebkitBackdropFilter: "blur(0px)",
                    border: "1px solid " + `rgb(${colors[theme]})`,
                    boxShadow: "inset 0 5px 20px " + `rgb(${colors[theme]})`,
                    zIndex: 1,
                    overflow: "hidden",
                }}
            >
                <Slide
                    in={transition}
                    style={{ position: "fixed", top: 0, zIndex: 2 }}
                >
                    <Paper
                        elevation={12}
                        sx={{
                            display: "flex",
                            alignItems: "center",
                            justifyContent: "center",
                            background: grey[900],
                            width: "100%",
                            borderBottom: "1px solid " + `rgb(${colors[theme]})`,
                        }}
                    >
                        <h1
                            style={{
                                width: "100%",
                                fontSize: 30,
                                textAlign: "center",
                            }}
                        >
                            <span style={{ color: `rgb(${colors[theme]})` }}>
                                <TypeAnimation
                                    sequence={[
                                        "Explore",
                                        1500,
                                        "Earn",
                                        1500,
                                        "Level-up",
                                        1500,
                                    ]}
                                    repeat={Infinity}
                                    speed={10}
                                    deletionSpeed={10}
                                />
                            </span>
                            !
                        </h1>
                    </Paper>
                </Slide>
                <Paper
                    sx={{
                        background: "rgba(50, 50, 50, 0.20)",
                        borderRadius: "10px",
                        boxShadow: "0 4px 30px rgba( 25, 25, 25, 0.8)",
                        backdropFilter: "blur(2px)",
                        WebkitBackdropFilter: "blur(2px)",
                        width: "100%",
                        height: "87%",
                        padding: 1.2,
                        border: "1px solid " + `rgb(${colors[theme]})`,
                        marginTop: "5.5rem",
                        overflow: "auto",
                    }}
                >
                    <Grid container="true" rowGap={1.2} width="100%">
                        {tasks.map((task, index) => (
                            <TaskCard
                                setBalance={setBalance}
                                balance={balance}
                                tasks={tasks}
                                setTasks={setTasks}
                                setSnackBar={setSnackBar}
                                key={index}
                                axios={axios}
                                task={task}
                                setUpgradeDrawer={setUpgradeDrawer}
                                colors={colors}
                                theme={theme}
                                friendLength={friendLength}
                            />
                        ))}
                    </Grid>
                </Paper>
                <SwipeableDrawer
                    keepMounted
                    PaperProps={{
                        sx: {
                            background: grey[900],
                            borderRadius: "12px 12px 0 0",
                            boxShadow: `0 -3px 18px rgb(${colors[theme]})`,
                            borderTop: `1px solid rgb(${colors[theme]})`,
                            textAlign: "center",
                            padding: "5px",
                            paddingBottom: "0px",
                        },
                    }}
                    slotProps={{
                        backdrop: {
                            sx: { bgcolor: "rgba(15, 15, 15, .90)" },
                        },
                    }}
                    anchor="bottom"
                    sx={{ zIndex: 999999 }}
                    allowSwipeInChildren={true}
                    disableSwipeToOpen={true}
                    open={upgradeDrawer[0]}
                    onOpen={() => {}}
                    title="Upgrade"
                    onClose={() => {
                        setUpgradeDrawer([false, <></>]);
                    }}
                >
                    {upgradeDrawer[1]}
                </SwipeableDrawer>
            </Grid>
        </div>
    );
};

export default Tasks;
