import { ErrorOutline } from "@mui/icons-material";
import { Alert, CircularProgress, LinearProgress } from "@mui/material";
import Grid from "@mui/material/Unstable_Grid2";
import React, { useState } from "react";

const TaskDrawerContent = ({
    task,
    axios,
    theme,
    friendLength,
    setSnackBar,
    tasks,
    setTasks,
    setUpgradeDrawer,
    setBalance,
    balance,
}) => {
    const [loading, setLoading] = useState(false);
    const mapped_tasks = task.prize.map((item, index) => (
        <Grid
            xs={3}
            key={index}
            className={task.level - 1 < index ? "btn white" : `btn ${theme}`}
            width={"30%"}
            padding={0.7}
        >
            <img height={"35px"} src="/assets/images/logo1.png" alt="" />
            <div style={{ marginTop: "-.5rem" }}>{item.toLocaleString()}</div>
        </Grid>
    ));

    return (
        <div
            style={{
                padding: 4,
            }}
        >
            <Grid marginTop={2} container="true">
                <Grid
                    xs={12}
                    display={"flex"}
                    justifyContent={"center"}
                    alignItems={"center"}
                >
                    <img
                        height={"70px"}
                        src="/assets/images/logo1.png"
                        alt=""
                    />
                    <span style={{ fontSize: "35px" }}>
                        {task.prize.length == 1
                            ? task.prize[0]
                            : task.prize[task.level]}
                    </span>
                </Grid>
                {task.prize.length > 1 ? (
                    <Grid
                        xs={12}
                        marginTop={3.5}
                        display={"flex"}
                        justifyContent={"center"}
                    >
                        <Grid
                            container="true"
                            gap={1.1}
                            maxWidth={"70%"}
                            display={"flex"}
                            justifyContent={"center"}
                            alignItems={"center"}
                        >
                            {mapped_tasks}
                        </Grid>
                    </Grid>
                ) : (
                    ""
                )}
            </Grid>
            <br />
            <div
                style={{
                    letterSpacing: "1px",
                }}
            >
                {task.description}
                {task.type == 3 ? (
                    <div>
                        <center>
                            <LinearProgress
                                value={
                                    (friendLength / task.data.minimum) * 100 >=
                                    100
                                        ? 100
                                        : (friendLength / task.data.minimum) *
                                          100
                                }
                                color={
                                    (friendLength / task.data.minimum) * 100
                                        ? "warning"
                                        : "success"
                                }
                                variant="determinate"
                                style={{
                                    maxWidth: "20rem",
                                }}
                            />
                        </center>
                        {/* <span
                            style={{
                                fontSize: "1.5rem",
                            }}
                        >{`${friendLength} / ${task.data.minimum}`}</span> */}
                    </div>
                ) : (
                    ""
                )}
                <br />
                <Alert
                    style={{ textAlign: "start" }}
                    variant="outlined"
                    color="error"
                    icon={<ErrorOutline />}
                >
                    <span
                        style={{
                            color: "white",
                            fontFamily: "sans-serif",
                            fontSize: "14px",
                        }}
                    >
                        <b>Attention</b>: Our system diligently reviews your
                        claim history. Please refrain from making false claims;
                        doing so will result in a <b>twofold</b> reduction of
                        your account balance.
                    </span>
                </Alert>
            </div>
            <br />
            <button
                className={
                    task.claimed || loading ? "btn white" : `btn ${theme}`
                }
                onClick={() => {
                    setLoading(true);
                    axios
                        .post("/task/submit", {
                            id: task.id,
                        })
                        .then(({ data }) => {
                            const index = tasks.findIndex(
                                (item) => item.id == task.id
                            );
                            tasks[index].claimed = true;
                            const newTasks = [...tasks];
                            if (tasks[index].level) {
                                newTasks[index].level += 1;
                                setTasks(newTasks);
                            }
                            setSnackBar(["Task completed.", "success", true]);
                            setUpgradeDrawer([false]);
                            setBalance(
                                balance +
                                    task.prize[
                                        task.type != 2 ? 0 : task.level - 1
                                    ]
                            );
                        })
                        .catch((error) => {
                            console.log(error);
                            if (error.response?.reset) {
                                const index = tasks.findIndex(
                                    (item) => item.id == task.id
                                );
                                const newTasks = [...tasks];
                                newTasks[index].level = 0;
                                setTasks(newTasks);
                            }
                            if (
                                error.response?.status == 409 &&
                                error.response?.data?.status
                            ) {
                                const index = tasks.findIndex(
                                    (item) => item.id == task.id
                                );
                                const newTasks = [...tasks];
                                newTasks[index].claimed = true;
                                setTasks(newTasks);
                            }
                            setSnackBar([
                                error.response?.data?.message ||
                                    "Error fetching data. Plese check your connection.",
                                "error",
                                true,
                            ]);
                        })
                        .finally(() => {
                            setLoading(false);
                        });
                }}
                style={{
                    padding: "10px",
                    color: "white",
                    fontSize: "30px",
                    fontFamily: "mortal-kombat",
                    marginBottom: "7px",
                    textAlign: "center",
                }}
                disabled={
                    loading ||
                    task.claimed ||
                    (task.type == 3 && task.data.minimum > friendLength)
                }
            >
                <div
                    style={{
                        display: "flex",
                        alignItems: "center",
                    }}
                >
                    {loading ? <CircularProgress /> : ""}
                    <span
                        style={
                            loading
                                ? {
                                      marginLeft: "8px",
                                  }
                                : {}
                        }
                    >
                        {loading
                            ? "Fetching data"
                            : task.type == 3 && task.data.minimum > friendLength
                            ? `${friendLength} / ${task.data.minimum}`
                            : task.claimed
                            ? "Claimed"
                            : "Claim"}
                    </span>
                </div>
            </button>
        </div>
    );
};

export default TaskDrawerContent;
